import './App.css';
import React, {useState, useEffect} from 'react'
import temple from './temple.png'

function Withdraw({withdrawMoney, handleWithdraw, withdraw}) {
    return (
        <div id="withdraw-div">
            
                <h3>Withdraw Amount: <span className='balance-amount'>${withdraw}</span></h3>
            <div>
            <div  id = 'addStuff'> 
            <div>
                <button className = "quick-btn" onClick={()=>withdrawMoney(20)}>$20</button>
                <button className = "quick-btn" onClick={()=>withdrawMoney(80)}>$80</button>
            </div>
            <div>
            <button className = "quick-btn" onClick={()=>withdrawMoney(40)}>$40</button>
            <button className = "quick-btn" onClick={()=>withdrawMoney(100)}>$100</button>
            </div>
            <div>
                <button className = "quick-btn" onClick={()=>withdrawMoney(60)}>$60</button>
                <button className = "quick-btn" onClick={()=>withdrawMoney(200)}>$200</button>
            </div> 
            <div>Quick Cash</div>      
            <div id = "other-withdraw">
                <input type = 'number' id = 'withdraw' onChange = {handleWithdraw}></input>
                <button id = 'other-button'onClick = {()=>withdrawMoney(Number(document.getElementById('withdraw').value))}>Other Amount</button>
            </div>
        </div>
        </div>
    </div>
    )
};

function Deposit({handleDeposit, depositMoney, deposit}) {
    return(
        <div id="deposit-div">
            <h3>Deposit Amount: <span className='balance-amount'>${deposit}</span></h3>
            <input type = 'number' id = 'deposit' onChange = {handleDeposit}></input>
            <button type = 'button' id = 'deposit-button' onClick = {depositMoney}>Deposit</button>
        </div>
    )
}

function HandleHistory({ accountHistory , pin }){

return (
    <div className = "history-item">
        {accountHistory[pin].map((item)=>item.withdraw ? <li style={{color:'red'}}>Withdrew <strong>${item.withdraw}</strong> | New Balance:<strong>${item.account}</strong></li> : <li style={{color:'green'}}>Deposited <strong>${item.deposit}</strong> | New Balance:<strong>${item.account}</strong></li>)}
    </div>
)
}

function ATM() {

    const [accountBalance, setAccountBalance] = useState({});
    const [withdraw, setWithdraw] = useState(0);
    const [deposit, setDeposit] = useState(0);
    const [accountHistory, setAccountHistory] = useState({});
    const [isWithdraw, setIsWithdraw] = useState(false);
    const [isDeposit, setIsDeposit] = useState(false);
    const [isSelected, setIsSelected] = useState(true);
    const [selectedResponse, setSelectedResponse] = useState(false)
    const [historyShow, setHistoryShow] = useState(false);
    const [isVerified, setIsVerified] = useState(false);
    const [pinCode , setPinCode] = useState([]);
    const [pinDisplay, setPinDisplay] = useState('')

    let pin = pinCode.join('')

    const withdrawMoney = (amount)=>{
        if((accountBalance[pin] - amount) < 0){
            return alert(`You don't have the funds!`)
        }else{
        let newAmount = accountBalance[pin] - amount;
        setAccountBalance(accountBalance, accountBalance[pin] = newAmount);
        setAccountHistory(accountHistory, accountHistory[pin].push({withdraw:amount.toLocaleString('en-us'),account:newAmount.toLocaleString('en-us')}));
        setWithdraw(amount);
        document.getElementById('withdraw').value = 0;
        }
    }

    const handleWithdraw = ()=>{
        let newwithdraw = document.getElementById('withdraw').value;
        setWithdraw(newwithdraw);
    }

    const handleDeposit = ()=>{
        let newDeposit = document.getElementById('deposit').value;
        setDeposit(newDeposit);
    }

    const depositMoney = ()=>{
        let deposit = Number(document.getElementById('deposit').value);
        let newAmount = accountBalance[pin] + deposit;
        setAccountBalance(accountBalance, accountBalance[pin] = newAmount);
        setAccountHistory(accountHistory,accountHistory[pin].push({deposit:deposit.toLocaleString('en-us'),account:newAmount.toLocaleString('en-us')}));
        console.log(accountHistory)
        setDeposit(0);
        document.getElementById('deposit').value = 0;
    }
    const handleSelection = (event)=>{
        if (event.target.style.backgroundColor === 'red'){
            event.target.style.backgroundColor = 'white'; 
            event.target.style.fontFamily = 'siliconhollow'
            setSelectedResponse(false);    
            setIsWithdraw(false);
            setIsDeposit(false);
        }else{
            event.target.style.backgroundColor = 'red';
            if(event.target === document.getElementById('withdrawBtn') && document.getElementById('withdrawBtn').style.backgroundColor === 'red'){
                document.getElementById('depositBtn').style.backgroundColor = 'white';
                document.getElementById('depositBtn').style.fontFamily = 'siliconhollow';
                event.target.style.fontFamily = 'siliconfilled';
                setSelectedResponse(true); 
                setIsSelected(false);
                setIsWithdraw(true);
                setIsDeposit(false);
            }else{ 
                document.getElementById('depositBtn').style.backgroundColor = 'red'
                document.getElementById('withdrawBtn').style.backgroundColor = 'white'
                document.getElementById('withdrawBtn').style.fontFamily = 'siliconhollow';
                event.target.style.fontFamily = 'siliconfilled'
                setSelectedResponse(true); 
                setIsSelected(false);
                setIsWithdraw(false);
                setIsDeposit(true);
            }    
        }

    }
    const selectNumber = (e)=>{
        let len = pinDisplay.length;
        let asteriskArr = Array(len).fill('*');
        asteriskArr.push(e.target.value)
        setPinDisplay([...asteriskArr])
        setPinCode([...pinCode, e.target.value])
    }
    const clearPin = ()=>{
        setPinDisplay('');
        setPinCode([]);
    }
    const undoPin = ()=>{
        setPinDisplay(pinDisplay.slice(0,pinDisplay.length-1));
        setPinCode([...pinCode.slice(0,pinCode.length-1)]);
    }
    const enterPin = ()=>{
        if(pinCode.length < 4) { 
            document.getElementById('pin-title').innerHTML = 'Pin must be 4 or More'
            document.getElementById('pin-title').style.color = 'red'
        }else{
        setIsVerified(true);
        (accountBalance[pin]) ? (console.log(accountBalance[pin])) : setAccountBalance(accountBalance, accountBalance[pin]= 0);
        (accountHistory[pin]) ? console.log(accountHistory[pin]) : setAccountHistory(accountHistory, accountHistory[pin]=[]);
        console.log(accountHistory)   
        setPinDisplay('')
        }
    }

    const endSession = ()=>{
        setPinCode([]);
        setIsVerified(false)
    }

    const showHistory = ()=>{
        let width = document.getElementById('interactive').style.width
        !historyShow ? width = '65%' : width = '100%';
        document.getElementById('interactive').style.width = width;
        let text = document.getElementById('history-button').innerHTML;
        !historyShow ? text = 'Hide Account History' : text = 'Show Account History';
        document.getElementById('history-button').innerHTML = text;
        !historyShow ? setHistoryShow(true) : setHistoryShow(false);
    }

    return (
        <div id= "brick-wall">
            <div id="atm-screen-div">
            <img src={temple} id = "temple"/>
            <div id="atm-title">REACT BANK ATM</div>
            {!isVerified && 
            <div id = "verification">
                <div id="inner-verification">
                    <div id="verification-title">
                        <h2 id="pin-title">Enter Pin</h2>
                    </div>
                    <div id ="pin-display">
                        <h3>{pinDisplay}</h3>
                    </div>
                    <div id = "keypad">
                        <div className = "grid-row">
                            <button key = '1' className = "keypad-btn" onClick = {selectNumber} value = '1' >1</button>
                            <button key = '2' className = "keypad-btn" onClick = {selectNumber} value = '2'>2</button>
                            <button key = '3' className = "keypad-btn" onClick = {selectNumber} value = '3'>3</button>
                        </div>
                        <div className = "grid-row">
                            <button key = '4' className = "keypad-btn" onClick = {selectNumber} value = '4'>4</button>
                            <button key = '5' className = "keypad-btn" onClick = {selectNumber} value = '5'>5</button>
                            <button key = '6' className = "keypad-btn" onClick = {selectNumber} value = '6'>6</button>
                        </div>
                        <div className = "grid-row">
                            <button key = '7' className = "keypad-btn" onClick = {selectNumber} value = '7'>7</button>
                            <button key = '8' className = "keypad-btn" onClick = {selectNumber} value = '8'>8</button>
                            <button key = '9' className = "keypad-btn" onClick = {selectNumber} value = '9'>9</button>
                        </div>
                        <div className = "grid-row">
                            <button key = 'clear' className = "keypad-btn" onClick = {clearPin}>Clear</button>
                            <button key = '0' className = "keypad-btn" onClick = {selectNumber} value = '0'>0</button>
                            <button key = '<' className = "keypad-btn" onClick = {undoPin}>&lt;</button>
                        </div>
                        <div id = "submit-pin">
                            <button id = "submit-btn" onClick = {enterPin}>Submit</button>
                        </div>
                    </div>
                </div>
            </div>}
            { isVerified &&
            <div id = "atm-screen">
                <div id = "interactive">
                    <h1 id = "account-balance">Account Balance<br/><span className='balance-amount'>${accountBalance[pin]}</span></h1>
                    {
                    isSelected &&
                    <div id = "button-div">
                        <div>
                            <button onClick={handleSelection} id = 'withdrawBtn'>Withdraw</button>
                        </div>
                        <div>
                            <button onClick={handleSelection} id = 'depositBtn'>Deposit</button>
                        </div>
                        <div>
                            <button type='button' id="history-button" onClick={showHistory}>Show Account History</button>
                        </div>
                        <div>
                            <button id = "end-session" onClick = {endSession}>End Session</button>
                        </div>
                    </div>
                    }
                    {
                    selectedResponse &&
                    <div id = "input-screen">
                        {isWithdraw && <div><Withdraw withdrawMoney = {withdrawMoney} handleWithdraw = {handleWithdraw} withdraw = {withdraw}/><button type='button' onClick={()=> {setIsSelected(true);setSelectedResponse(false)}}>Main Menu</button></div>}
                        {isDeposit && <div><Deposit depositMoney = {depositMoney} handleDeposit = {handleDeposit}  deposit = {deposit} /><button type='button' onClick={()=> {setIsSelected(true);setSelectedResponse(false)}}>Main Menu</button></div>}
                    </div>
                    }
                </div>
                {
                    historyShow &&
                <div key = 'history' id = 'history'>
                    <ul id="history-list">
                        <h3 id = "history-title">Account History</h3>
                        { <HandleHistory accountHistory = {accountHistory} pin = {pin} />}
                    </ul>
                </div>
                }
            </div>
            }
            </div>
        </div>
    )
}

export default ATM

